import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import Map from '../../components/map';
import wutil from '../../lib/wifet-util';

const MIN = 20;
const MAX = 60;

const initialState = {
	aiData: wutil.makeRandomData(500, MIN, MAX),
	windData: wutil.makeRandomData(500, MIN, MAX),
};

@observer
class Production extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { windData, aiData } = this.state;
		const { appState, windmillStore } = this.props.stores;
        const { darkmode } = appState;
		const { windmills } = windmillStore;

		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1><i class='fas fa-chart-bar' /> Production</h1>
						<LineGraph 
							data={windData}
							data2B={aiData}
							legend='Production'
							legend2B='Production AI prediction'
							width={1200}
							height={400}
							yMax={100}
							y2Max={100}
							showYTicks={1}
							yTicks='["40 MWh", "30 MWh", "20 MWh", "10 MWh", "0 MWh"]'
							showXTicks={1}
						/>
					</div>
				</div>
				<div class='row'>
					<div class='col-12' style='height: 50vh;'>
						<Map stores={this.props.stores}
							darkmode={darkmode}
							center={[67.1253653, 12.422578125]}
							zoom={13}
							waypoints={windmills}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Production;
